import React from 'react'; import styled from 'styled-components'; 
// ----------------
import NeonLogo from '~Client/Logo/NeonLogo';
const MainText = (props) => {
    return (
        <>
            <div style={{display: 'flex', justifyContent: 'space-evenly'}}> <MainHeader>Hello World</MainHeader> <NeonLogo logo='Contact' /> </div>
            <MainHeaderText>Добро пожаловать! Это приложение находится в стадии *Pre-Alpha*.</MainHeaderText>
            <MainHeaderText>В скорем будущем планируется реализовать навигацию как в операционых системах Windows, MacOS.</MainHeaderText>
            <MainHeaderText>Если вы бы хотели поделится своими идеями, или вы просто комуникативный человек <br /> жмите на *Contact* и подписывайтесь или добавляйтесь в друзья :).</MainHeaderText>
        </>
    )
}
const MainHeader = styled.h1 `font-size: 150%; text-shadow: 1px 1px 2px #f9f;
     @media (max-width: 500px) { font-size: 120%; }`

const MainHeaderText = styled.p `font-weight: bold; text-align: center; margin-top: 10px; padding-bottom: 10px; font-size: 130%; border-bottom: 1px solid #000; 
    @media (max-width: 500px) { font-size: 100%; }`



export default MainText